.header{
  padding: 10rem 25%;
  background: linear-gradient(90deg, #77f5ef4d 0%, rgba(45, 135, 219, 0.274) 100%), url("../images/header.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  font-style: italic;
  color: linear-gradient(90deg, rgba(27, 144, 199, 0.774) 0%, rgba(103, 115, 224, 0.733) 100%);
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  justify-content: center;
}

.subTitle{
  color:rgb(228, 228, 228);
}