.contacts {
  width: 100%;
  height: 50px;
  background-color: #d4d4d4;
  color: #fff !important;
  text-align: right !important;

}

#menu ul li { display: inline; 
    list-style:none;}

#menu ul li a {
    display: inline-block;
    /* visual do link */
    color: #333;
    text-decoration: none;
    font-size: 20px;
}

#menu ul li a:hover {
    background-color:#D6D6D6;
    color: #6D6D6D;
}