@media (min-width: 1081px) {
.menu {
  width: 100%;
  height: 80px;
  background-color: rgb(255, 255, 255);
}

#menu ul {
    padding:10px;
    margin:0px;
    list-style:none;
    height: 100%;
}

.vaca {
  left: 0%;
}

#menu ul li { display: inline; 
    list-style:none;}

#menu ul li a {
    display: inline-block;
    /* visual do link */
    color: #333 !important;
    text-decoration: none;
    font-size: 20px !important;
}

#menu ul li a:hover {
    background-color:#D6D6D6;
    color: #6D6D6D;
}
}