a {
  color: #fff;
  text-decoration: none;
}

.p-navegacao{
   height: 100%;
   z-index: 999;
   float: right;
   position: absolute;
}

/* Hamburger */
.hamburger1 {
  height: 45px;
  display: -ms-grid;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  justify-items: center;
  z-index: 120;
}

.hamburger1 div {
  /* background-color: rgb(41, 88, 158); */
  background-color: rgb(255, 255, 255);
  position: relative;
  width: 15px;
  height: 3px;
  margin-top: 7px;
  align-items: center;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.menuDiv {
    position: fixed;
    right: 15px;
    top: 15px;
    width: 50px;
    height: 50px;
    text-align: center;
    background: #000000;
    line-height: 50px;
    z-index: 999;
    border-radius: 5px;
}

#checkbox {
  display: none;
}

#checkbox:checked ~ .navegacao {
  height: 120%;
}

/* Menu */
.navegacao {
  position: fixed;
  background-color: #212529 !important;
  list-style: none;
  width: 15%;
  float: right;
  text-align: left;
  height: 0px;
  overflow: hidden;
  transition: height .3s ease;
  z-index: 100;
  -webkit-transition: all 0.5s ease;
  transition: all 0.4s ease;
  right: 0%;
}

@media (max-width: 992px) {
  .navegacao {
    position: fixed;
    background-color: #212529 !important;
    list-style: none;
    width: 45%;
    float: right;
    text-align: left;
    height: 0px;
    overflow: hidden;
    transition: height .4s ease;
    z-index: 100;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    right: 0%;
  }
}


li {
  padding: 20px !important;
}
.font {
  margin: 0;
  padding: 0px 30px 0px 30px;
  font: 700 16px 'Oswald', sans-serif;
}

.font:hover {
  background-color: rgba(131, 127, 127, 0.541);
  color: rgb(61, 61, 61);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  width: 100%;
}