.about{
  padding: 10rem 25%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.774) 0%, rgba(255, 255, 255, 0.733) 100%), url("../images/services.jpg");
  background-color: whitesmoke;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

  font-style: italic;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  justify-content: center;
}

.about-image {
   width: 100%;
 border-Radius:50px;
 position: relative;
}